import React, { Component } from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink
} from "react-router-dom"
import $ from "jquery"
import logo from "./logo.svg"
import Home from "./Home"
import Sample from "./pages/Sample"
import Blank from "./pages/Blank"
import Newsletter from "./Newsletter"
import Footer from "./Footer"
import Login from "./Login"
import Register from "./Register"
import { FaBars } from 'react-icons/fa'
import { FaSignInAlt } from 'react-icons/fa'
import { FaUserPlus } from 'react-icons/fa'

export default class Template extends Component {
  componentDidMount () {
    $(".navigation-toggle").click(function(){
      $(".navigation").toggle(400);
      $(".navigation-toggle").toggleClass("active");
    });
    $(".navigation").find("a").click(function(){
      $(".navigation").toggle(200);
      $(".navigation-toggle").removeClass("active");
    });

    $(window).scroll(function() {
      if ($(this).scrollTop()) {
          $('.backtotop').fadeIn();
      } else {
          $('.backtotop').fadeOut();
      }
  });
  $(".backtotop").click(function() {
      $("html, body").animate({scrollTop: 0}, 1000);
   });

  }
  render() {
      return (
    <Router>
      <div>
        <div className="header-wrapper">
          <div className="container">
          <div className="header">
          <NavLink to="/"><img src={logo} alt={"logo"}/></NavLink>
            </div>
        <div className="navigation">
        <ul>
        <li>
          <NavLink exact to="/">Home</NavLink>
          </li>
          <li>
            <NavLink to="/sample-page">Sample Page</NavLink>
          </li>
          <li>
            <NavLink to="/blank">Blank Page</NavLink>
          </li>
        </ul>
        </div>
        <span className="navigation-toggle"><FaBars/></span>
        <div className="sign-nav">
          <ul>
            <li><NavLink className="login" to="/login"><FaSignInAlt/><span className="txt">Login</span></NavLink></li>
            <li><NavLink className="register" to="/register"><FaUserPlus/><span className="txt">Register</span></NavLink></li>
          </ul>
        </div>
        </div>
        </div>
        <Switch>
        <Route exact path="/" component={Home}/>
        <Route exact path="/sample-page" component={Sample}/>
        <Route exact path="/blank" component={Blank}/>
        <Route exact path="/login" component={Login}/>
        <Route exact path="/register" component={Register}/>
        </Switch>
        <Newsletter/>
        <Footer/>
      </div>
    </Router>
  );
      }
}